import { App } from './App';
import { AppContainer } from 'react-hot-loader';
import React from 'react';
import ReactDom from 'react-dom';
import { LogProvider } from "./Providers/LogProvider";
import { LogSearch } from "./Search"; // Enable React devtools

window.React = React;
var logProvider = new LogProvider(50);
OCA.Search.logreader = new LogSearch(logProvider);

function render(App, rootElement) {
  ReactDom.render(React.createElement(AppContainer, null, React.createElement(App, {
    logProvider: logProvider,
    inlineSettings: rootElement.dataset.inlineSettings === 'true'
  })), rootElement);
}

